<template>
  <section id="projects" class="section-wrapper">
    <h2 class="section-header">Projects</h2>
    <div class="project">
      <h3 class="project-title">Take it, Bake it, Make it</h3>
      <h4 class="project-language">React JS</h4>
      <div class="project-description">
        <p>
          Take it, Bake it, Make it is a proof of concept project I helped to develop during my time
          in Ahold Delhaize's Propulsion Lab. It gives the customer a number of meals to choose
          from, and the option to have them ready to eat straight from the store, ready to put in
          the oven at home, or the ingredients ready to cook themselves.
        </p>
        <p>The website is comprised of a customer facing web app, and a store facing admin app.</p>
      </div>
      <b-card no-body>
        <b-tabs card>
          <b-tab title="Customer Side" active>
            <b-container>
              <b-row>
                <b-col lg="8">
                  <h4>Customer Side</h4>
                  <p>
                    The customer side of this application was designed using a mobile first
                    approach, to accommodate the fast paced, on the go schedule of a customer that
                    would likely use the service. A desktop design was later designed and developed
                    as well.
                  </p>
                  <p>
                    The following features were implemented for the customer facing side of this
                    application:
                  </p>
                  <ul>
                    <li>Log in</li>
                    <li>Select a store</li>
                    <li>Select a service (Take, Bake, Make)</li>
                    <li>Browse meals (Can be different based on service selection)</li>
                    <li>
                      View details, including directions for cooking or heating if Bake or Make are
                      selected
                    </li>
                    <li>Add meals to cart</li>
                    <li>Adjust service or quantity from the cart screen</li>
                    <li>Choose a pickup time</li>
                    <li>Receive a confirmation for your order</li>
                  </ul>
                </b-col>
                <b-col lg="4">
                  <CustomerCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
          <b-tab title="Admin Side">
            <b-container>
              <b-row>
                <b-col lg="4">
                  <h4>Admin Side</h4>
                  <p>
                    The admin side of this application was developed for desktop or landscape iPad
                    screens, as these are the devices that a store associate would have access to.
                  </p>
                  <p>
                    The following features were implemented for the administration facing side of
                    this application:
                  </p>
                  <ul>
                    <li>
                      Active orders: Orders that have been submitted to the store, either pending
                      acceptance or currently being prepared. Can be filtered based on service
                    </li>
                    <li>
                      Ready orders: orders that are completed and waiting to be picked up by the
                      customer.
                    </li>
                    <li>Past orders: Search through previous orders based on date.</li>
                  </ul>
                </b-col>
                <b-col lg="8">
                  <AdminCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
    <div class="project">
      <h3 class="project-title">Who's Who</h3>
      <h4 class="project-language">React JS</h4>
      <div class="project-description">
        <p>
          Who's Who was a rapidly developed application for a private conference. The goal was to
          give participants a basic, Facebook or Twitter like interface to post their thoughts and
          opinions regarding the conference in real time, and see what other users were saying as
          well.
        </p>
        <p>
          Over the course of two weeks this application was built from the ground up, from
          requirements gathering to design, development, testing and deployment.
        </p>
        <p>
          In addition to being one of two front end developers on the project, I worked closely with
          the other parts of the team every step of the way. This gave me valuable insight into the
          concerns of our project management, UX designers, backend developers and QA team. Through
          team work and compromises, we were able to release a fully developed and tested product
          that satisfied all requirements on schedule.
        </p>
      </div>
      <b-card no-body>
        <b-tabs card>
          <b-tab title="Mobile" active>
            <b-container>
              <b-row>
                <b-col lg="8">
                  <h4>Mobile</h4>
                  <p>
                    This site was designed mobile first. Its focus was on maintaining a simple, self
                    explanatory user experience and clean, clear user interface.
                  </p>
                  <p>The following features were implemented:</p>
                  <ul>
                    <li>Profile with fun facts and relevant information</li>
                    <li>Newsfeed showing posts from everyone at the conference</li>
                    <li>Comments on posts</li>
                    <li>Searching through users</li>
                  </ul>
                </b-col>
                <b-col lg="4">
                  <WWMobileCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
          <b-tab title="Desktop">
            <b-container>
              <b-row>
                <b-col lg="4">
                  <h4>Desktop</h4>
                  <p>
                    Making the site desktop friendly had its challenges, such as ensuring support
                    for Internet Explorer and varying screen sizes.
                  </p>
                  <p>The same features were implemented for the desktop application as mobile:</p>
                  <ul>
                    <li>Profile with fun facts and relevant information</li>
                    <li>Newsfeed showing posts from everyone at the conference</li>
                    <li>Comments on posts</li>
                    <li>Searching through users</li>
                  </ul>
                </b-col>
                <b-col lg="8">
                  <WWDesktopCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
    <div class="project">
      <h3 class="project-title">Muse</h3>
      <h4 class="project-language">Swift, Kotlin</h4>
      <div class="project-description">
        Muse is a project that started from struggles I have as a musician. When writing music, I
        frequently record short ideas to my voice memos to return to later. However, this can
        quickly become cluttered and disorganized, allowing good ideas to fall through the cracks.
        This application hopes to avoid this pitfall by giving the user multiple means of organizing
        their recordings, such as by groups or in a calendar view to see their progress later.
      </div>
      <b-card no-body>
        <b-tabs card>
          <b-tab title="iOS">
            <b-container>
              <b-row>
                <b-col lg="8">
                  <h4>iOS</h4>
                  <p>
                    The iOS app was a learning experience for me. Although I had some Swift
                    experience from my time at Ahold, it was my first time diving into a project
                    completely on my own, and learning more advanced topics such as audio recording,
                    file management, JTAppleCalendar, and audio playback.
                  </p>
                </b-col>
                <b-col lg="4">
                  <MuseIOSCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
          <b-tab title="Android" active>
            <b-container>
              <b-row>
                <b-col lg="8">
                  <h4>Android</h4>
                  <p>
                    Muse is my largest Android project to date. Although it has it is not yet
                    finished, it has taught me a lot about programming in Android, including data
                    binding, audio recording and playback, fragments and much more. I hope to add more
                    to this project in the future, such as improved UI, sorting recordings by groups,
                    and using a third party calendar to allow for indicators on dates with recordings.
                  </p>
                </b-col>
                <b-col lg="4">
                  <MuseAndroidCarousel />
                </b-col>
              </b-row>
            </b-container>
          </b-tab>
        </b-tabs>
      </b-card>
      <div class="project">
        <h3 class="project-title">This Website</h3>
        <h4 class="project-language">Vue.js</h4>
        <div class="project-description">
          This website was my first completed project in Vue.js. I used BootstrapVue and SCSS to
          create the styling, and Font Awesome for the icons. I drew influence from
          <a
            href="https://themes.3rdwavemedia.com/demo/developer/"
          >this template</a>, but did not
          use any code from it. It was fun getting to learn a new skill while creating something to
          display my other skills!
        </div>
      </div>
    </div>
  </section>
</template>

<style lang="scss">
