      //START OVERRIDE DEFAULT BOOSTRAP CAROUSEL
      &.portrait {
        max-width: 230px;
        .carousel-item {
          height: 495px;
        }
      }

      &.landscape {
        width: 600px;
        max-width: 100%;
        .carousel-item {
          height: 330px;
        }
      }

      & .carousel-item {
        &.active {
          display: flex;
          align-items: center;
        }

        &.carousel-item-right,
        &.carousel-item-left {
          display: flex;
          align-items: center;
        }
      }

      .carousel-caption {
        p {
