          padding-bottom: 5px;
        }

        position: absolute;
        bottom: 1px;
        right: 0;
        left: 0;
        padding: 0;

        background: rgba(0, 0, 0, 0.5);
      }
      .carousel-control-next-icon,
      .carousel-control-prev-icon {
        background-color: rgba(0, 0, 0, 0.5);
        background-size: 20px;
        padding: 15px;
        border-radius: 50%;
        &.carousel-control-next-icon {
          background-position: 5.5px;
        }
        &.carousel-control-prev-icon {
          background-position: 3.5px;
        }
      }
      .carousel-indicators {
        margin-bottom: -10px;
      }
    }
    //END OVERRIDE DEFAULT BOOTSTRAP CAROUSEL
  }
}
</style>

<script>
import CustomerCarousel from "./../components/projects/TbmitCustomerCarousel";
import AdminCarousel from "./../components/projects/TbmitAdminCarousel";
import WWMobileCarousel from "./../components/projects/WhosWhoMobileCarousel";
import WWDesktopCarousel from "./../components/projects/WhosWhoDesktopCarousel";
import MuseAndroidCarousel from "./../components/projects/MuseAndroidCarousel";
import MuseIOSCarousel from "./../components/projects/MuseIOSCarousel";

export default {
  name: "projects",
  components: {
    CustomerCarousel,
    AdminCarousel,
    WWMobileCarousel,
    WWDesktopCarousel,
