<template>
  <div>
    <b-navbar
      ref="nav"
      id="navbar"
      toggleable="sm"
      fixed="top"
      type="dark"
      variant="dark"
      v-b-scrollspy.80
    >
      <b-navbar-toggle target="nav-text-collapse"></b-navbar-toggle>
      <b-collapse id="nav-text-collapse" is-nav>
        <b-navbar-nav pills>
          <b-nav-item href="#intro" @click="scrollIntoView">Home</b-nav-item>
          <b-nav-item href="#about" @click="scrollIntoView">About</b-nav-item>
          <b-nav-item href="#projects" @click="scrollIntoView">Projects</b-nav-item>
          <b-nav-item href="#experience" @click="scrollIntoView">Experience</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <div ref="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "nav-bar",
  methods: {
    // Convenience method to scroll a heading into view.
    scrollIntoView(evt) {
      evt.preventDefault();
      const href = evt.target.getAttribute("href");
      const el = href ? document.querySelector(href) : null;
      if (el) {
        document.documentElement.scrollTop = this.elementOffset(el);
      }
    },
    elementOffset(el) {
      return (
        el.getBoundingClientRect().top +
        window.scrollY -
        this.$refs.nav.offsetHeight -
        20
      ); // offset of the element - height of navbar - 20 px for row padding
    }
  }
};
</script>

<style scoped lang="scss">
@import "./../_global.scss";
</style>